.product{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    background-color: white ;
    margin: 10px;
    z-index: 1;
    padding: 20px;
    width: 100%;
}
.product_price{
    margin-top: 5px;

}
.product_info{
    height: 100px;
    margin-bottom: 15px;
}
.product > button{
    background-color: #f0c14b ;
    border: 1px solid;
    border-color: #a88734 #9c7e31 #846a29;

}
.product > img{
    width: 100%;
    max-height: 200px;
    object-fit: contain;
    margin-bottom: 15px;
}
.product_rating{
    display: flex;

}