.checkoutProduct{
    display: flex;
    margin-top: 20px;
    margin-bottom: 20px;

}
.checkoutProduct_rating{
    display: flex;

}
.checkoutProduct_image{
    object-fit: contain;
    width: 180px;
    height: 180px;

}
.checkoutProduct_title{
    font-size: 17px;
    font-weight: 800;
    
}
.checkoutProduct_info{
    padding-left: 20px;
}
.checkoutProduct_info > button {
    background-color: #f0c14b ;
    border: 1px solid;
    border-color: #a88734 #9c7e31 #846a29;
    margin-top: 10px;
    color: #111;
}