.home{
    max-width: 1500px;
    margin-left: auto;
    margin-right: auto;
}
.home_row{
    display: flex;
    z-index: 1;
    margin-left: 5px;
    margin-right: 5px;

}
.home_row > image{
    object-fit: contain;
    max-height: 130px;
    display: flex;
}
.home_image{
    width: 100%;
    mask-image: linear-gradient(to bottom,rgb(0,0,0,1),rgb(0,0,0,0));
    z-index:-1;
    margin-bottom: -90px;
}