.header
{
    background-color:#131921;
    display: flex;
    align-items: center;
    position: sticky;
    top: 0;
    z-index: 100;
}
.header_logo
{
    object-fit: contain;
    width: 100px;
    margin-top: 18px;
    margin-left: 20px;
    margin-right: 20px;
}
.header_optionBasket{
    color: white;
    display: flex;
    align-items: center;
}
.header_basketCount{
    margin-left: 10px;
    margin-right: 10px;
}
.headerNav{
    display: flex;
    justify-content: space-evenly;
}
.header_option{
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    margin-right: 10px;

}
.header_optionLineOne{
    font-size: 10px;
}
.header_optionLineTwo{
    font-size: 13px;
    font-weight: 800;
}
.header_link{
    color: white;
    text-decoration: none;
}
.header_search{
    display: flex;
    flex: 1;
}
.header_searchInput{
    height: 12px;
    padding: 10px;
    border: none;
    width: 100%;
}
.header_searchIcon{
    background-color: orange;
    padding: 5px;
    height: 22px !important;
}